<template>
  <div id="app">
    <header>
      <div>
        <label for="title">ToDoList</label>
        <input id="title" type="text" placeholder="请输入事项" v-model="todo" @keyup.13="submit">
      </div>
    </header>
    <section>
      <h2>正在进行<span>{{ todonumber }}</span></h2>
      <ul>
        <li class="todolist" v-for="(item, index) in todolist" :key="index" v-show="!item.isCompleted">
          <input class="checkbox" type="checkbox" v-model="item.isCompleted">
          <p @dblclick="edit(index)" v-if="!item.edit">{{ item.text }}</p>
          <input class="edit" type="text" v-if="item.edit" @blur="finish(index)" @keyup.13="$event.target.blur" v-model="newVal" v-focus>
          <a class="delete" @click="del(index)">-</a>
        </li>
      </ul>
      <h2>已经完成<span>{{ donenumber }}</span></h2>
      <ul>
        <li class="donelist" v-for="(item,index) in todolist" :key="index" v-show="item.isCompleted">
          <input class="checkbox" type="checkbox" v-model="item.isCompleted">
          <p><del @dblclick="edit(index)" v-if="!item.edit">{{ item.text }}</del></p>
          <input class="edit" type="text" v-if="item.edit" @blur="finish(index)" v-model="newVal">
          <a class="delete" @click="del(index)">-</a>
        </li>
      </ul>
    </section>
  </div>
</template>


<script>
export default {
  name: 'app',
  data: function() {
    return {
      todo: '',
      todolist: [],
      show: true,
    }
  },
  directives: {
    focus: {
      inserted: function(el) {
        el.focus();
      }
    }
  },
  computed: {
    todonumber: function() {
      return this.todolist.filter((item) => {
        return !item.isCompleted
      }).length;
    },
    donenumber: function() {
      return this.todolist.length-this.todonumber;
    }
  },
  methods: {
    submit: function() {
      this.todolist.push({
        text: this.todo,
        isCompleted: false,
        edit: false,
      });
      this.todo = '';
    },
    del: function(i) {
      this.todolist.splice(i,1);
    },
    edit: function(i) {
      var oldVal = this.todolist[i];
      oldVal.edit = !oldVal.edit;
      this.todolist.splice(i,1,oldVal);
      this.newVal = oldVal.text;
    },
    finish: function(i) {
      var oldVal = this.todolist[i];
      oldVal.edit = !oldVal.edit;
      oldVal.text = this.newVal;
      this.todolist.splice(i,1,oldVal);
    }
  },
  mounted: function() { 
    var element = document.createElement("script");
    element.src = "http://open.sojson.com/common/js/canvas-nest.min.js";
    element.count = "200";
    element.zindex = "-2";
    element.opacity = "0.5";
    element.color = "47,135,193";
    element.type = "text/javascript";
    document.body.appendChild(element);
  }
}
</script>

