  border-radius: 5px;
  box-shadow: 0 1px 6px #888 inset;
}

section{
  width: 600px;
  margin: 0 auto;
}

section h2{
  margin: 20px 0;
  position: relative;
}

section span{
  float: right;
}

section li{
  height: 32px;
  line-height: 32px;
